import React, {Component} from 'react';
import logo from './logo.svg';
import './App.css';

class ReviewTable extends Component {
  numbers = ['1', '2', '3', '4', '7'];
  formula = ['NH4', 'NO2', 'NO3', 'SO3', 'SO4', 'HSO4', 'OH', 'PO4', 'HPO4', 'H2PO4', 'CO3', 'HCO3',
    'ClO4', 'ClO3', 'ClO2', 'ClO', 'C2H3O2', 'Cr2O7', 'CrO4', 'CN'];
  charges = ['+', '-', '-', '2-', '2-', '-', '-', '3-', '2-', '-', '2-', '-', '-', '-', '-', '-', '-', '2-', '-', '-'];
  name = ['AMMONIUM', 'NITRITE', 'NITRATE', 'SULFITE', 'SULFATE', 'BISULFATE', 'HYDROXIDE', 'PHOSPHATE', 'HYDROGEN PHOSPHATE',
    'DIHYDROGEN PHOSPHATE', 'CARBONATE', 'BICARBONATE', 'PERCHLORATE', 'CHLORATE', 'CHLORITE', 'HYPOCHLORITE',
    'ACETATE', 'DICHROMATE', 'CHROMATE', 'CYANIDE'];
  getFormula = (i) => {
    let f = this.formula[i];
    let out = '';
    let n;
    for (n = 0; n < f.length; n ++) {
      if (this.numbers.includes(f[n])) {
        out += f[n].sub();
      } else {
        out += f[n];
      }
    }
    out += this.charges[i].sup();
    return out;
  };
  render() {
    if (this.props.visible) {
      let out = [];
      let i;
      for (i = 0; i < this.formula.length; i ++) {
        out.push(
            <tr>
              <td dangerouslySetInnerHTML={{__html: this.getFormula(i)}}/>
              <td>{this.name[i].toLowerCase()}</td>
            </tr>
        );
      }
      return (
          <table>
            {out}
          </table>
      );
    }
    return null;
  }
}

class Card extends Component{
  constructor(props) {
    super(props);
    this.state = {
      score: 0,
      index: 0,
      answer: '',
      random: false,
      review: false,
      charge: '3-',
    }
  }
  answer = '';
  charge = '3-';
  numbers = ['1', '2', '3', '4'];
  formula = ['NH4', 'NO2', 'NO3', 'SO3', 'SO4', 'HSO4', 'OH', 'PO4', 'HPO4', 'H2PO4', 'CO3', 'HCO3',
    'ClO4', 'ClO3', 'ClO2', 'ClO', 'C2H3O2', 'Cr2O7', 'CrO4', 'CN'];
  charges = ['+', '-', '-', '2-', '2-', '-', '-', '3-', '2-', '-', '2-', '-', '-', '-', '-', '-', '-', '2-', '-', '-'];
  getFormula = (i) => {
    let f = this.formula[i];
    let out = '';
    let n;
    for (n = 0; n < f.length; n ++) {
      if (this.numbers.includes(f[n])) {
        out += f[n].sub();
      } else {
        out += f[n];
      }
    }
    out += this.state.charge.sup();
    return out;
  };
  name = ['AMMONIUM', 'NITRITE', 'NITRATE', 'SULFITE', 'SULFATE', 'BISULFATE', 'HYDROXIDE', 'PHOSPHATE', 'HYDROGEN PHOSPHATE',
  'DIHYDROGEN PHOSPHATE', 'CARBONATE', 'BICARBONATE', 'PERCHLORATE', 'CHLORATE', 'CHLORITE', 'HYPOCHLORITE',
    'ACETATE', 'DICHROMATE', 'CHROMATE', 'CYANIDE'];
  checkAnswer = () => {
    if (this.answer === this.name[this.state.index] && this.charge === this.charges[this.state.index]) {
      if (this.state.random) {
        this.setState({index: Math.floor(Math.random() * this.formula.length), score: this.state.score + 1});
      } else {
        if (this.state.index === this.name.length - 1) {
          this.setState({index: 0});
        } else {
          this.setState({index: this.state.index + 1});
        }
        this.setState({score: this.state.score + 1});
      }
      console.log('CORRECT!!!');
    }
  };
  onClick = (e) => {
    this.setState({answer: e.target.value.toUpperCase()});
    this.answer = e.target.value.toUpperCase();
    this.checkAnswer();
  };
  onCheck = (e) => {
    console.log(e.target.checked);
    this.setState({random: e.target.checked});
  };
  onReview = (e) => {
    this.setState({review: !this.state.review});
  };
  selectCharge = (e) => {
    this.setState({charge: e.target.value});
    this.charge = e.target.value;
    this.checkAnswer();
  };
  render() {
    return(
        <div>
          <div className={'card'}>
            <label>
              <input type={'checkbox'} onChange={this.onCheck}/>
              Random order
            </label>
            <h3>Score: {this.state.score}</h3>
            <h1 dangerouslySetInnerHTML={{__html: this.getFormula(this.state.index)}}/>
            <label>Name of ion: </label>
            <input disabled={this.state.review} onChange={this.onClick}/>
            <br/>
            <label>Charge: </label>
            <select disabled={this.state.review} onChange={this.selectCharge}>
              <option value="3-">3-</option>
              <option value="2-">2-</option>
              <option value="-">-</option>
              <option value="+">+</option>
            </select>
            <h2>{this.state.answer}</h2>
            <button onClick={this.onReview}>Review</button>
          </div>
          <br/>
          <ReviewTable visible={this.state.review}/>
        </div>
    );
  }
}

function App() {
  return (
    <div className={'main'}>
      <br/>
      <Card/>
      <br/>
    </div>
  );
}

export default App;
